import React from "react";
import "./servise.css"
import "./../../../App.css"
import {NavLink} from "react-router-dom";

const Service = () => {
  return (
    <div className="servises-block">
       <div className="about__service wrapper">
            <h2 className="about__h2-title"> </h2>
            <div className="service__items">
              <div className="service__item">
                <h3 className="service__title"> VR/AR</h3>
                <p className="service__info">     . /   360</p>
                <NavLink className="services__link" to="/services#vr"></NavLink>
              </div>
              <div className="service__item">
                <h3 className="service__title"> </h3>
                <p className="service__info">   .    .     </p>
                <NavLink className="services__link" to="/services#laser"></NavLink>
              </div>
              <div className="service__item">
                <h3 className="service__title"> </h3>
                <p className="service__info">. Arduino IDE . Unity / Unreal Engine 3D-. Blender / Cinema 4D</p>
                <NavLink className="services__link" to="/services#course"></NavLink>
              </div>
              <div className="service__item">
                <h3 className="service__title"></h3>
                <p className="service__info"> 3D-  .      </p>
                <NavLink className="services__link" to="/services#3d"></NavLink>
              </div>
            </div>
          </div>
    </div>
     
  )
}

export default Service